import { graphql, navigate, useStaticQuery } from "gatsby"
import * as React from "react"
import Button_ from "./Button_"

const AppFooter = () => {
  const data_ = useStaticQuery(graphql`
  query FooterQuery {
    site {
      siteMetadata {
        author
        title
        nav {
          text
          to
        }
      }
    }
  }
`)
  return (
    <div className="w-[98%] md:w-4/5 select-none lg:w-3/5 left-0 right-0 m-auto relative mt-3">
      <div className="flex flex-col items-center justify-center mb-4 h-12 rounded-md">
        <div className="flex flex-row gap-3">
        <Button_ onClick={()=>navigate("/")} className="font-bold transition-all">{data_.site.siteMetadata.title}</Button_>
        <Button_ onClick={()=>navigate("/about")} className="font-bold transition-all">{data_.site.siteMetadata.author}</Button_>
        <Button_ onClick={() => window.scrollTo(0, 0)}>回到顶部</Button_>
        </div>
      </div>
    </div>
  )
}

export default AppFooter