import * as React from "react"
import Dec from "./Dec"
import Button_ from "./Button_"
import { navigate } from "gatsby"
const Readme = () => {
    return (
        <>
        <div>
        <div className="text-5xl font-bold select-none dark:text-gray-50">一个<div className="text-cf h-14">WEB开发者</div></div>
        </div>
        <div className="dark:text-gray-50 pt-2 text-gray-400 text-lg select-none"><Dec /></div>
        <div className="pt-2 mt-2 border-t border-dashed border-gray-300 dark:border-gray-800">
            <img src="https://img.shields.io/badge/%E6%9C%80%E5%90%8E%E6%9B%B4%E6%96%B0%E6%97%B6%E9%97%B4-2024%2F3%2F9-%23444?style=flat" alt=""/>
        </div>
        <div className="mt-2 grid gap-2 lg:grid-cols-6 md:grid-cols-5 grid-cols-2">
            <Button_ onClick={()=>navigate("https://github.com/zwtghm")}>我的Github</Button_>
        </div>
        </>
    )
}

export default Readme