import React from "react"

const Button_ = ({children,onClick}:any) => {
    return (
      <div className="dark:bg-gray-800 dark:text-gray-50 text-center dark:border-gray-600 select-none bg-gray-100 border border-gray-400 inline-block pt-1 pb-1 pl-2 pr-2 font-semibold rounded-md" onClick={onClick}>
        {children}
      </div>
    )
  } 

  export default Button_