import { PageProps } from "gatsby"
import * as React from "react"
import AppContent from "../components/AppContent"
import Readme from "../components/Readme"

const AboutPage: React.FC<PageProps> = () => {
    return (
        <AppContent setTheme={undefined}>
            <div className="word-break mb-4 select-none dark:text-gray-50 break-all"><div className="text-xl font-semibold md:text-start text-center">README&nbsp;❗</div></div>
            <Readme />
            <div className="pt-4 mt-4 border-t border-gray-300 dark:border-gray-800">
                <div className="text-xl">abababababa</div>
                <div className="text-8xl">香蕉 🍌</div>
                <div className="text-8xl">巧克力 🍫</div>
            </div>
        </AppContent>
    )
}
export default AboutPage